.about {
  min-height: 100%;
}

.about__avatar {
  width: 200px;

  -webkit-transform: scale(1.0);

      -ms-transform: scale(1.0);

          transform: scale(1.0);
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
}

.about__avatar:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
}

.about__name {
  font-size: 25px;
}

.about__description {
  width: 700px;
  margin: 0 auto 0 auto;
}


